<script setup>
import { onMounted } from 'vue';
import { RouterLink } from 'vue-router';

// Icon components
import ButtonClose from '@/components/icons/ButtonClose.vue';
import SoccerBallIconVue from '@/components/icons/SoccerBallIcon.vue.vue';

defineProps({
	config: {
		type: Object,
		default: () => ({
			logo: {
				image: '/logo-ph.png',
				name: 'Sport Management',
				size: 40,
			},
			links: [
				{ name: 'Nosotros', to: '#about' },
				{ name: 'Servicios', to: '#services' },
				{ name: 'Jugadores', to: '#players' },
				{ name: 'Contacto', to: '#contact' },
			],
		}),
	},
});

const handleShowMenu = () => {};

onMounted(() => {
	let barNavigation = document.querySelector('.navbar');

	window.addEventListener('scroll', () => {
		if (document.documentElement.scrollTop >= 20) {
			barNavigation.classList.add('sticky');
		} else {
			barNavigation.classList.remove('sticky');
		}
	});
});
</script>

<template>
	<nav class="navbar bg-white z-3">
		<div class="container d-flex">
			<div class="row w-100">
				<div class="col">
					<div class="box-logo">
						<RouterLink
							to="#contact"
							class="logo__router-link text-decoration-none d-flex gap-3"
						>
							<img
								:src="config.logo.image"
								:width="config.logo.size"
								alt="Logo"
								class="logo__image"
							/>

							<h1 class="fs-6 fs-lg-5 m-0 text-black">
								{{ config.logo.name.split(' ')[0] }}
								<p class="m-0">{{ config.logo.name.split(' ')[1] }}</p>
							</h1>
						</RouterLink>
					</div>
				</div>
				<div class="col-9 d-none d-lg-block">
					<ul
						class="box-link justify-content-end list-unstyled gap-5 d-none d-lg-flex align-items-center mb-0 h-100"
					>
						<li>
							<RouterLink
								class="router-link text-decoration-none text-dark"
								active-class="active"
								to="/"
							>
								Home
							</RouterLink>
						</li>
						<li v-for="link in config.links" :key="link.name">
							<a
								class="router-link text-decoration-none text-dark"
								active-class="active"
								:href="link.to"
							>
								{{ link.name }}
							</a>
						</li>
					</ul>
				</div>
				<!-- <div class="col d-flex justify-content-end align-items-center">
					<SoccerBallIconVue
						class="animate__animated animate__bounceIn d-lg-none"
						:color="'white'"
						:size="32"
						@click="handleShowMenu"
					/>
				</div> -->
			</div>

			<!-- <div class="nav-link-mobile position-relative">
				<div class="container position-absolute">
					<ul
						class="box-link justify-content-end list-unstyled gap-4 d-flex flex-column d-lg-none align-items-center mb-0 h-100"
					>
						<li v-for="link in config.links" :key="link.name">
							<router-link
								class="router-link text-decoration-none text-dark fs-2"
								active-class="active"
								:to="link.to"
								>{{ link.name }}</router-link
							>
						</li>
					</ul>
				</div>
			</div> -->
		</div>
	</nav>
</template>

<style lang="scss" scoped>
.navbar {
	padding: 16px 0;
	transition:
		padding 0.35s ease-in-out,
		box-shadow 0.35s ease-in-out;

	&.sticky {
		position: static;
		left: 0;
		top: 0;
		right: 0;
		padding: 32px 0;
		box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
	}
}
</style>
