<script setup>
import { onMounted, ref } from 'vue';

const toGoUp = () => scrollTo(0, 0);

const isShowed = ref(false);
const isScrollOnTheTop = ref(document.documentElement.scrollTop != 0);

onMounted(() => {
	window.addEventListener('scroll', () => {
		if (!isScrollOnTheTop.value) {
			isShowed.value = true;
		} else {
			isShowed.value = false;
		}
	});
});
</script>

<template>
	<div @click="toGoUp" id="go-up" class="go-up" :class="{ mostrar: isShowed }">
		<svg
			xmlns="http://www.w3.org/2000/svg"
			width="50px"
			height="50px"
			viewBox="0 0 24 24"
			fill="none"
		>
			<path
				d="M6 15L12 9L18 15"
				stroke="white"
				stroke-width="2"
				stroke-linecap="round"
				stroke-linejoin="round"
			/>
		</svg>
	</div>
</template>

<style scoped>
.go-up {
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	right: 25px;
	bottom: 25px;
	background-color: var(--bs-primary);
	width: 30px;
	height: 30px;
	color: var(--bs-white);
	text-align: center;
	border-radius: 50%;
	transition: all 0.25s ease-out;
	opacity: 0;
}

#go-up:hover {
	cursor: pointer;
	transform: scale(1.1);
}

.button-up {
	width: 15px;
	-webkit-filter: drop-shadow(5px 5px 5px #666666);
	filter: drop-shadow(5px 5px 5px #666666);
}

.mostrar,
.go-up .mostrar {
	transition: all 0.65s ease-in;
	opacity: 1;
}

@media only screen and (min-width: 1024px) {
	#go-up {
		width: 35px;
		height: 35px;
	}
	.button-up {
		width: 15px;
	}
}
</style>
